import { Injectable } from '@angular/core';
import {Http} from '@angular/http';
import "rxjs/add/operator/map";

@Injectable()
export class StatisticsService {

  constructor(public http:Http) { }

  totalBinders()
  {
    return this.http.get("/totalBinders").map(resp => resp.json());
  }

  totalDocuments()
  {
    return this.http.get("/totalDocuments").map(resp => resp.json());
  }

  //search documents by binder.id
  searchDocument5(id:number)
  {
    return this.http.get("/searchDocument5?binder="+id+"").map(resp => resp.json());
  }

}



