import { Component, OnInit } from '@angular/core';
import {Http} from '@angular/http';
import {BinderService} from '../services/binder.service';
import {StatisticsService} from '../services/statistics.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-statistics',
  templateUrl: './statistics.component.html',
  styleUrls: ['./statistics.component.css']
})
export class StatisticsComponent implements OnInit {

  pageBinders:any;

  countBinders:any;

  countDocuments:any;

  deletable: any;

  constructor(public http:Http , public binderService:BinderService, public statisticsService:StatisticsService,public router:Router) { }

  ngOnInit() {
    this.getAllBinders();
    this.totalBinders();
    this.totalDocuments();
  }


  getAllBinders()
  {
    this.binderService.getAllBinders()
      .subscribe(data=>{
        this.pageBinders = data;
      }, err=>{
        alert("غير موجود");
      });
  }

  totalBinders()
  {
    this.statisticsService.totalBinders()
      .subscribe(data=>{
        this.countBinders = data;
      }, err=>{
        alert("غير موجود");
      });
  }

  totalDocuments()
  {
    this.statisticsService.totalDocuments()
      .subscribe(data=>{
        this.countDocuments = data;
      }, err=>{
        alert("غير موجود");
      });
  }


  onEditBinder(id:number)
  {
      this.router.navigate(['editBinder',id]);
  }

  onDeleteBinder(id:number,i:number)
  {

    this.statisticsService.searchDocument5(id)
      .subscribe(data=>{
        if(data.totalElements == 0){
          if(confirm("تأكيد الحذف")) {
            this.binderService.deleteBinder(id)
              .subscribe(data=>{
                this.pageBinders.splice(i,1);
                alert("تم الحذف بنجاح");
              }, err=>{
                alert("غير موجود");
              });
          }
        }else{ alert("لا يمكن حذف هذا الملف");}
        console.log(data.totalElements);
      }, err=>{
        console.log(err);
      });
  }



}



