import { Component, OnInit } from '@angular/core';
import {BinderService} from '../services/binder.service';
import {Http} from '@angular/http';
import {DocumentService} from '../services/document.service';
import {Document} from '../model/model.document';
import {ActivatedRoute, Router} from '@angular/router';
import {FullDocument} from '../model/model.fullDocument';

@Component({
  selector: 'app-edit-document',
  templateUrl: './edit-document.component.html',
  styleUrls: ['./edit-document.component.css']
})
export class EditDocumentComponent implements OnInit {
  pageBinders:any;
  id:number;
  i: any;

  fullDocument:FullDocument = new FullDocument();

  constructor(public http:Http , public binderService:BinderService,public documentService:DocumentService
                ,public activatedRoute:ActivatedRoute,public router:Router) {
    this.id = activatedRoute.snapshot.params['id'];
    console.log(activatedRoute.snapshot.params['id']);
  }

  ngOnInit() {

    //fillCombbobox binders
    this.binderService.getAllBinders()
      .subscribe(data=>{
        this.pageBinders = data;
      }, err=>{
        alert("غير موجود");
      });

    this.getDocument();
  }

  getDocument(){
    this.documentService.getDocument(this.id)
      .subscribe(data=>{
        this.fullDocument = data;
      }, err=>{
        alert("غير موجود");
      });
  }

  updateDocument()
  {
    this.i = this.pageBinders.findIndex(b => b.title==this.fullDocument.binder.title);

    this.fullDocument.binder.id = this.pageBinders[this.i].id;
    this.fullDocument.binder.description = this.pageBinders[this.i].description;

    this.documentService.updateDocument(this.id,this.fullDocument)
      .subscribe(data=>{
        alert("تم التحديث بنجاح");
        this.router.navigate(['search']);
      },err=>{
        console.log(err);
        alert("لم يحفظ");
      });

  }

}



