import { Component, OnInit } from '@angular/core';
import {Binder} from '../model/model.binder';

import {BinderService} from '../services/binder.service';

@Component({
  selector: 'app-binder',
  templateUrl: './binder.component.html',
  styleUrls: ['./binder.component.css']
})
export class BinderComponent implements OnInit {

  binder:Binder = new Binder();

  constructor(public binderService:BinderService) { }

  ngOnInit() {

  }

  saveBinder(){
    this.binderService.saveBinder(this.binder)
      .subscribe(data=>{
        alert("تمت الإضافة بنجاح");
        console.log(data);
        this.binder.title = "";
        this.binder.description="";
      },err=>{
        console.log(err);
        alert("لم يحفظ");
      });
  }





}



