import { Injectable } from '@angular/core';
import "rxjs/add/operator/map";
import {Http} from '@angular/http';
import {Document} from '../model/model.document';
import {FullDocument} from '../model/model.fullDocument';


@Injectable()
export class DocumentService {

  constructor(public http:Http) {
  }

    saveDocument(document:Document)
    {
      return this.http.post("/document",document).map(resp => resp.json());
    }

    getAllDocument()
    {
      return this.http.get("/document").map(resp => resp.json());
    }

    getDocument(id:number)
    {
      return this.http.get("/document/"+id).map(resp => resp.json());
    }

    updateDocument(id:number,fullDocument:FullDocument)
    {
      return this.http.put("/document/"+id,fullDocument).map(resp => resp.json());
    }

    deleteDocument(id:number)
    {
      return this.http.delete("/document/"+id).map(resp => resp.json());
    }

    //no date
    searchDocument1(fullDocument:FullDocument,page:number,size:number)
    {
        return this.http.get("/searchDocument?title="+fullDocument.title+
          "&desc="+fullDocument.description+"&binder="+fullDocument.binder.title+
          "&sender="+fullDocument.sender+"&original="+fullDocument.original+
          "&receiver="+fullDocument.receiver+"&page="+page+"&size="+size+"").map(resp => resp.json());
    }


    //date entree
    searchDocument2(fullDocument:FullDocument,page:number,size:number)
    {
      return this.http.get("/searchDocument2?title="+fullDocument.title+
        "&desc="+fullDocument.description+"&binder="+fullDocument.binder.title+
        "&sender="+fullDocument.sender+"&original="+fullDocument.original+
        "&receiver="+fullDocument.receiver+"&date_entree="+fullDocument.date_entree+"&page="+page+"&size="+size+"").map(resp => resp.json());
    }


    //date sortie
    searchDocument3(fullDocument:FullDocument,page:number,size:number)
    {
      return this.http.get("/searchDocument3?title="+fullDocument.title+
        "&desc="+fullDocument.description+"&binder="+fullDocument.binder.title+
        "&sender="+fullDocument.sender+"&original="+fullDocument.original+
        "&receiver="+fullDocument.receiver+"&date_sortie="+fullDocument.date_sortie+"&page="+page+"&size="+size+"").map(resp => resp.json());
    }



    //both date
    searchDocument4(fullDocument:FullDocument,page:number,size:number)
    {
      return this.http.get("/searchDocument4?title="+fullDocument.title+
        "&desc="+fullDocument.description+"&binder="+fullDocument.binder.title+
        "&sender="+fullDocument.sender+"&original="+fullDocument.original+
        "&receiver="+fullDocument.receiver+"&date_entree="+fullDocument.date_entree+
        "&date_sortie="+fullDocument.date_sortie+"&page="+page+"&size="+size+"").map(resp => resp.json());
    }



}

















