import { Injectable } from '@angular/core';
import {Http} from '@angular/http';

import "rxjs/add/operator/map";
import {Binder} from '../model/model.binder';

@Injectable()
export class BinderService {

  constructor(public http:Http) { }



  getAllBinders()
{
  return this.http.get("/binder").map(resp => resp.json());
}

  getStatBinders()
  {
    return this.http.get("/countDocumentByBinder").map(resp => resp.json());
  }

  getBinder(id:number)
  {
    return this.http.get("/binder/"+id).map(resp => resp.json());
  }

  saveBinder(binder:Binder)
  {
    return this.http.post("/binder",binder).map(resp => resp.json());
  }

  updateBinder(id:number,binder:Binder)
  {
    return this.http.put("/binder/"+id,binder).map(resp => resp.json());
  }

  deleteBinder(id:number)
  {
    return this.http.delete("/binder/"+id).map(resp => resp.json());
  }

}




