import { Component, OnInit } from '@angular/core';
import {Binder} from '../model/model.binder';
import {BinderService} from '../services/binder.service';
import {Http} from '@angular/http';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
  selector: 'app-edit-binder',
  templateUrl: './edit-binder.component.html',
  styleUrls: ['./edit-binder.component.css']
})
export class EditBinderComponent implements OnInit {

  binder:Binder = new Binder();
  id:number;

  constructor(public binderService:BinderService,public activatedRoute:ActivatedRoute,
              public router:Router) {
    this.id = activatedRoute.snapshot.params['id'];
    console.log(activatedRoute.snapshot.params['id']);
  }



  ngOnInit() {
    this.getBinder();
  }

  getBinder()
  {
      this.binderService.getBinder(this.id)
        .subscribe(data=>{
          this.binder = data;
        }, err=>{
          alert("غير موجود");
        });
  }

  updateBinder(){
    this.binderService.updateBinder(this.id,this.binder)
      .subscribe(data=>{
        alert("تم التحديث بنجاح");
        this.router.navigate(['statistics']);
      }, err=>{
        alert("غير موجود");
      });
  }




}



