import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';


import { AppComponent } from './app.component';
import { BinderComponent } from './binder/binder.component';
import { DocumentComponent } from './document/document.component';

import { RouterModule, Routes } from '@angular/router';
import { SearchComponent } from './search/search.component';
import { StatisticsComponent } from './statistics/statistics.component';
import {BinderService} from './services/binder.service';
import {HttpModule} from '@angular/http';
import {FormsModule} from '@angular/forms';
import {DocumentService} from './services/document.service';
import {StatisticsService} from './services/statistics.service';
import { EditBinderComponent } from './edit-binder/edit-binder.component';
import { EditDocumentComponent } from './edit-document/edit-document.component';
import {UploadService} from './services/upload.service';
import {HttpClientModule} from '@angular/common/http';
import {SafePipe} from './services/safePipe.pipe';
import {HashLocationStrategy, LocationStrategy} from '@angular/common';

const routes: Routes =  [
  { path: 'binders', component: BinderComponent},
  { path: 'search', component: SearchComponent},
  { path: 'documents', component: DocumentComponent},
  { path: 'statistics', component: StatisticsComponent},
  { path: 'editBinder/:id', component: EditBinderComponent},
  { path: 'editDocument/:id', component: EditDocumentComponent},
  { path: '', redirectTo: '/search', pathMatch: 'full'}
];

@NgModule({
  declarations: [
    AppComponent,
    BinderComponent,
    DocumentComponent,
    SearchComponent,
    StatisticsComponent,
    EditBinderComponent,
    EditDocumentComponent,
    SafePipe
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routes),
    HttpModule,
    FormsModule,
    HttpClientModule
  ],
  providers: [BinderService, DocumentService,StatisticsService,UploadService],
  bootstrap: [AppComponent]
})
export class AppModule { }



