import { Component, OnInit } from '@angular/core';
import {Http} from '@angular/http';
import { HttpClient, HttpResponse, HttpEventType } from '@angular/common/http';
import {BinderService} from '../services/binder.service';
import {Document} from '../model/model.document';
import {DocumentService} from '../services/document.service';
import {UploadService} from '../services/upload.service';

@Component({
  selector: 'app-document',
  templateUrl: './document.component.html',
  styleUrls: ['./document.component.css']
})
export class DocumentComponent implements OnInit {
  pageBinders:any;
  path : any;
  i: any;

  document:Document = new Document();

  constructor(public http:Http , public binderService:BinderService, public documentService:DocumentService,
              public uploadService:UploadService) { }

  ngOnInit() {
    //fillCombbobox binders
    this.binderService.getAllBinders()
      .subscribe(data=>{
        this.pageBinders = data;
      }, err=>{
        alert("غير موجود");
      });
  }


  saveDocument(){

    this.i = this.pageBinders.findIndex(b => b.title==this.document.binder.title);

    this.document.binder.id = this.pageBinders[this.i].id;
    this.document.binder.description = this.pageBinders[this.i].description;

    this.documentService.saveDocument(this.document)
      .subscribe(data=>{
        this.upload(data.id);
        alert("تمت الإضافة بنجاح");
        console.log(data.id);
        this.document.title = "";
        this.document.sender = "";
        this.document.description = "";
        this.document.binder.title = "";
        this.document.original = null;
        this.path = "";
      },err=>{
        console.log(err);
        alert("لم يحفظ");
      });



  }

  selectedFiles: FileList;
  currentFileUpload: File;

  selectFile(event) {
    this.selectedFiles = event.target.files;
  }

  upload(id:number) {

    this.currentFileUpload = this.selectedFiles.item(0);
    this.uploadService.pushFileToStorage(this.currentFileUpload,id).subscribe(event => {
      if (event.type === HttpEventType.UploadProgress) {
        console.log("progresssss");
      } else if (event instanceof HttpResponse) {
        console.log('File is completely uploaded!');
      }
    });

    this.selectedFiles = undefined;
  }

}



